"use client"
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  ResponsiveContainer
} from "recharts"

export default function ForecastChart({ historyData, forecastData }) {
  if (!forecastData || forecastData.length === 0) {
    return (
      <div className="bg-white shadow rounded-lg p-4 mt-6">
        <p className="text-gray-500">Forecast will appear once the model runs.</p>
      </div>
    )
  }

  const combined = [
    ...historyData.map(d => ({
      year: d.year,
      totalIncome: d.totalIncome,
      type: "Actual"
    })),
    ...forecastData.map(f => ({
      year: f.year,
      totalIncome: f.totalIncome,
      type: "Forecast"
    }))
  ]

  return (
    <div className="bg-white shadow rounded-lg p-4 mt-6">
      <h3 className="text-lg font-semibold mb-2">Forecasted Income (3 Years)</h3>

      <ResponsiveContainer width="100%" height={350}>
        <LineChart data={combined}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="year" />
          <YAxis tickFormatter={v => `${(v / 1_000_000).toFixed(1)}M`} />
          <Tooltip />

          {/* Actual */}
          <Line
            dataKey="totalIncome"
            stroke="#4F46E5"
            strokeWidth={3}
            dot={{ r: 4 }}
            isAnimationActive={false}
          />

          {/* Forecast (dashed) */}
          <Line
            dataKey="totalIncome"
            stroke="#10B981"
            strokeDasharray="5 5"
            strokeWidth={3}
            dot={{ r: 4 }}
            isAnimationActive={false}
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  )
}
